import React, { useEffect, useMemo, useRef, useState } from "react";
import lasersword from "../../assets/audio/cursor/lasersword.wav";
import hammer from "../../assets/audio/cursor/klopf.wav";
import shot from "../../assets/audio/cursor/schuss.wav";
import sausage from "../../assets/audio/cursor/sausage.wav";

interface Props {
  cursorName: "hammer" | "lasersword" | "crosshair" | "sausage";
}

export const MouseCursor = ({ cursorName }: Props) => {
  // const mouseCursor = useRef<any>(null);
  console.log("render cursorPosition  mouse component");
  const [cursorPositionX, setCursorPositionX] = useState<number>(0);
  const [cursorPositionY, setCursorPositionY] = useState<number>(0);
  console.log(" cursorPosition cursorName", cursorName);
  console.log("cursorPositionX", cursorPositionX);
  console.log("cursorPositionY", cursorPositionY);

  useEffect(() => {
    console.log("render cursorPosition useeffect mouse component");
    switch (cursorName) {
      case "hammer":
        setCursorPositionX(20);
        setCursorPositionY(150);
        break;
      case "lasersword":
        setCursorPositionX(-70);
        setCursorPositionY(30);
        break;
      case "sausage":
        setCursorPositionX(-30);
        setCursorPositionY(250);
        break;
      default:
        setCursorPositionX(100);
        setCursorPositionY(100);
    }
  }, [cursorName]);

  const mouseCursor = useRef<HTMLDivElement | null>(null);
  const soundRef = useRef<HTMLAudioElement | null>(null);

  useEffect(() => {
    // Initialize the sound based on cursorName
    if (cursorName === "hammer") {
      soundRef.current = new Audio(hammer);
      soundRef.current.volume = 0.8;
      soundRef.current.playbackRate = 1.0;
    } else if (cursorName === "lasersword") {
      soundRef.current = new Audio(lasersword);
      soundRef.current.volume = 1;
      soundRef.current.playbackRate = 2.0;
    } else if (cursorName === "sausage") {
      soundRef.current = new Audio(sausage);
      soundRef.current.volume = 1;
      soundRef.current.playbackRate = 1.0;
    } else {
      soundRef.current = new Audio(shot);
      soundRef.current.volume = 0.8;
      soundRef.current.playbackRate = 2.0;
    }
  }, [cursorName]);

  const handleMouseUp = () => {
    // Play the sound on every mouseup
    if (soundRef.current) {
      soundRef.current.play();
    }
  };

  useEffect(() => {
    // Add mouseup event listener
    window.addEventListener("mouseup", handleMouseUp);

    // Clean up the event listener when the component unmounts
    return () => {
      window.removeEventListener("mouseup", handleMouseUp);
    };
  }, []);

  window.addEventListener("mousemove", cursor);
  window.addEventListener("mouseup", (e) => cursorImage(e, cursorName));
  window.addEventListener("mousedown", (e) => cursorImage(e, cursorName));

  //move hammer
  async function cursorImage(e: MouseEvent, cursorName: string) {
    console.log("e.eventPhase", e.type);

    if (mouseCursor.current !== null) {
      if (e.type === "mousedown") {
        console.log("mouseCursor.current", mouseCursor.current);
        mouseCursor.current.style.top = e.pageY - cursorPositionY + "px";
        mouseCursor.current.style.left = e.pageX - cursorPositionX + "px";
        mouseCursor.current.className = cursorName + ` animation-${cursorName}`;
      } else {
        console.log("mouseCursor.current", mouseCursor.current);
        mouseCursor.current.style.top = e.pageY - cursorPositionY + "px";
        mouseCursor.current.style.left = e.pageX - cursorPositionX + "px";
        mouseCursor.current.className = cursorName;
      }
    }
  }

  function cursor(e: MouseEvent) {
    if (mouseCursor.current !== null) {
      console.log("mouseCursor.current", mouseCursor.current);
      mouseCursor.current.style.top = e.pageY - cursorPositionY + "px";
      mouseCursor.current.style.left = e.pageX - cursorPositionX + "px";
    }
  }

  return <div ref={mouseCursor} className={cursorName}></div>;
};
