import React, { useEffect, useRef, useState } from "react";
import { Board } from "../components/board/Board";
import "../components/board/Board.css";
import kennyGameScreen from "../svg/background/spaceKenny.png";
import JumperComponent, {
  IJumper,
} from "../components/jumperComponent/JumperComponent";
import kennyImage from "../svg/kenny.png";
import { useServices } from "../services/ServiceProvider";
import { GameInfos } from "../services/game-service/game-service";
import { useStartGame } from "../hooks/useStartGame";
import { FormControl, Grid, LinearProgress, TextField } from "@mui/material";
import { useRanking } from "../hooks/useRanking";
import { MouseCursor } from "../components/mouseCursor/MouseCursor";

const publishDate = new Date("2023-10-19T10:00:00.000Z");
//const today = new Date("2023-10-19T11:02:00.000Z");
const today = new Date();
const contdown = publishDate.getTime() - today.getTime();
const days = Math.floor(contdown / (1000 * 60 * 60 * 24));
var hours = Math.floor((contdown % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
var minutes = Math.floor((contdown % (1000 * 60 * 60)) / (1000 * 60));
var seconds = Math.floor((contdown % (1000 * 60)) / 1000);

export const GamePage = () => {
  console.log("RENDER GAMEPAGE");
  const [gameInfos, setGameInfos] = useState<GameInfos | null>(null);
  const [play, setPlay] = useState<boolean>(false);
  const [firstName, setFirstName] = useState<string>("");
  const [fightName, setFightName] = useState<string>("");

  const { gameService } = useServices();
  const {
    data: ranking,
    isLoading: isLoadingRanking,
    error: rankingError,
  } = useRanking(gameInfos?.totalScore);
  const { mutate: startGame, error, isLoading } = useStartGame();

  useEffect(() => {
    const id = gameService.addListener((gameinfos) => {
      setGameInfos(gameinfos);
    });
    return () => gameService.removeListener(id);
  }, [gameService]);

  console.log(
    "gameService._jumpersArray",
    gameService._jumpersArray
  ); /*todo redners every second*/

  if (error || rankingError) {
    return <div>{"Server is down"}</div>;
  }

  return (
    <>
      {play === false ? (
        <div
          className={"board"}
          style={{
            width: "100%",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
          }}
        >
          <img src={kennyGameScreen} style={{ width: "100%" }} />
          {contdown > 0 ? (
            <h2
              style={{ color: "white" }}
            >{`${days}Tage ${hours}h ${minutes}min ${seconds}s`}</h2>
          ) : (
            <button className={"button"} onClick={() => setPlay(true)}>
              Spielen
            </button>
          )}
        </div>
      ) : (
        <>
          <div className="container">
            <Board
              className="board"
              onStartGame={() => {
                gameService.startNewLevel();
              }}
              gameInfos={gameInfos}
              jumpers={gameService._jumpersArray ?? []}
              isLoading={isLoading}
            />
            {!localStorage.getItem("id") && (
              <div className="boardStartDialogContainer">
                <div className={"boardStartDialog"}>
                  <h1>{"Das Spiel beginnen lassen du musst!"}</h1>

                  <FormControl
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      padding: "30px",
                      gap: "30px",
                    }}
                  >
                    <TextField
                      label="Vorname"
                      value={firstName}
                      onChange={(e) => {
                        setFirstName(e.target.value);
                      }}
                    />
                    <TextField
                      label="Kampfname"
                      value={fightName}
                      onChange={(e) => {
                        setFightName(e.target.value);
                      }}
                    />
                    <button
                      className="button"
                      type={"submit"}
                      onClick={() => {
                        console.log(firstName, fightName);
                        startGame({ firstName, fightName });
                      }}
                    >
                      weiter
                    </button>
                  </FormControl>
                </div>
              </div>
            )}
          </div>

          {!isLoading ? (
            ranking && (
              <div style={{ padding: 30 }}>
                <table style={{ width: "100%" }}>
                  <tr>
                    <th align="left" style={{ padding: "20px" }}>
                      {""}
                    </th>
                    <th align="left" style={{ padding: "20px", opacity: 0.3 }}>
                      {"Spieler"}
                    </th>
                    <th
                      align="center"
                      style={{ padding: "20px", opacity: 0.3 }}
                    >
                      {"Shot Kenny`s"}
                    </th>
                    <th
                      align="center"
                      style={{ padding: "20px", opacity: 0.3 }}
                    >
                      {"Level"}
                    </th>
                  </tr>
                  {ranking.map((listing: any, index: any) => {
                    return (
                      <tr key={index}>
                        <td align="left">
                          {(() => {
                            switch (index + 1) {
                              case 1:
                                return (
                                  <h2
                                    style={{
                                      padding: 20,
                                      color: "gold",
                                      fontSize: "2rem",
                                    }}
                                  >
                                    {index + 1}
                                  </h2>
                                );
                              case 2:
                                return (
                                  <h2
                                    style={{
                                      padding: 20,
                                      color: "silver",
                                      fontSize: "2rem",
                                    }}
                                  >
                                    {index + 1}
                                  </h2>
                                );
                              case 3:
                                return (
                                  <h2
                                    style={{
                                      padding: 20,
                                      color: "#cd7f32",
                                      fontSize: "2rem",
                                    }}
                                  >
                                    {index + 1}
                                  </h2>
                                );
                              default:
                                return (
                                  <h2 style={{ padding: 20, opacity: 0.3 }}>
                                    {index + 1}
                                  </h2>
                                );
                            }
                          })()}
                        </td>
                        <td align="left">
                          {
                            <h2
                              style={{
                                padding: 20,
                                opacity: index + 1 > 3 ? 0.3 : 1,
                              }}
                            >
                              {listing.name}
                            </h2>
                          }
                        </td>
                        <td align="center">
                          {
                            <h2
                              style={{
                                padding: 20,
                                opacity: index + 1 > 3 ? 0.3 : 1,
                              }}
                            >
                              {listing.totalScore}
                            </h2>
                          }
                        </td>
                        <td align="center">
                          {
                            <h2
                              style={{
                                padding: 20,
                                opacity: index + 1 > 3 ? 0.3 : 1,
                              }}
                            >
                              {"LEVEL " + listing.level}
                            </h2>
                          }
                        </td>
                      </tr>
                    );
                  })}
                </table>
              </div>
            )
          ) : (
            <div style={{ zIndex: 1000, width: "50%" }} className="loading">
              <Grid xs item>
                <LinearProgress color={"primary"} />
              </Grid>
            </div>
          )}
          {rankingError ? <h2>{"Error loading Ranking"}</h2> : null}
        </>
      )}
    </>
  );
};
