import React, { useEffect, useState, useRef } from "react";
import JumperComponent from "./components/jumperComponent/JumperComponent";
import kennyImage from "./svg/kenny.png";
import { Board } from "./components/board/Board";
import "./App.css";
import { Player } from "./services/player-service/player-service";
import { Game } from "./services/game-service/game-service";
import { ServiceProvider } from "./services/ServiceProvider";
import { services } from "./services/services";
import { GamePage } from "./pages/GamePage";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import { MouseCursor } from "./components/mouseCursor/MouseCursor";

function assertDefined<T>(value: T | undefined, message: string): T {
  if (value === undefined) {
    throw new Error(message);
  }
  return value;
}

const baseUrlENV = process.env.REACT_APP_API_URL;
export const baseUrl: string = assertDefined<string>(
  baseUrlENV,
  "REACT_APP_API_URL is not defined."
);

function App() {
  // Create a client
  const queryClient = new QueryClient();

  return (
    <ServiceProvider services={services}>
      <QueryClientProvider client={queryClient}>
        <GamePage />
      </QueryClientProvider>
    </ServiceProvider>
  );
}

export default App;
