import { Jumper } from "../Jumper";
import jimmyImage from "./jimmy.png";
import jimmy from "./jimmy.wav";

export class JumperJimmy extends Jumper {
  constructor(onShotJumper: () => void, jumpHeight: number) {
    super(jimmyImage, onShotJumper);
    this.animation = this.animateJumper(50, 2000, jumpHeight, 5000);
  }
  onPlaySound = async () => {
    const sound = new Audio(jimmy);
    await sound.play();
  };
}
